.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #070f22;
  background-color: #f2ffff;
  padding: 16px 16px;
}
.navbar-logo {
  font-family: "Major Mono Display", monospace;
  font-size: 18px;
  font-weight: bold;
}
.navbar-links {
  /* display: inline-block; */
  font-size: 14px;
  background-color: #f2ffff;
}

.navbar a {
  color: #2c4361;
  text-decoration: none;
  padding: 10px;
  border-radius: 8px;
}

.portfolio-item-one-card,
.portfolio-item-two-card,
.portfolio-item-three-card {
  display: flex;
}
.card {
  flex: 1 0 100%;
  /*
  position: relative;
  background-size: cover; */
  background-repeat: no-repeat;
  background-position: 100% 50%;
}
.content-card-one {
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(44, 67, 97, 0.3)),
      to(rgba(44, 67, 97, 0.3))
    ),
    url(/static/media/vidly-feature-sunset-bg.17d58be7.jpg);
  background-image: linear-gradient(
      rgba(44, 67, 97, 0.3),
      rgba(44, 67, 97, 0.3)
    ),
    url(/static/media/vidly-feature-sunset-bg.17d58be7.jpg);
}
.content-card-two {
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(44, 67, 97, 0.3)),
      to(rgba(44, 67, 97, 0.3))
    ),
    url(/static/media/got-forest-bg.64394641.jpg);
  background-image: linear-gradient(
      rgba(44, 67, 97, 0.3),
      rgba(44, 67, 97, 0.3)
    ),
    url(/static/media/got-forest-bg.64394641.jpg);
}
.content-card-three {
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(205, 0, 130, 0.3)),
      to(rgba(205, 0, 130, 0.3))
    ),
    url("https://images.unsplash.com/photo-1548504778-b14db6c34b04?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2000&q=80");
  background-image: linear-gradient(
      rgba(205, 0, 130, 0.3),
      rgba(205, 0, 130, 0.3)
    ),
    url("https://images.unsplash.com/photo-1548504778-b14db6c34b04?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2000&q=80");
}
.header-one {
  font-size: 42px;
  padding-top: 50px;
  color: whitesmoke;
}
