.content-tech-one,
.content-tech-two,
.content-tech-three {
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  flex-flow: row wrap;
  align-content: flex-end;
  padding-bottom: 42px;
}
.tech-icons-container {
  display: flex;
  justify-content: center;
  height: 24px;
}
.header-two,
.header-three {
  font-size: 40px;
  padding-top: 50px;
  color: whitesmoke;
}
.subtext {
  font-family: "Muli", sans-serif;
  color: whitesmoke;
  font-size: 16px;
  padding: 8px;
}

.subtext-two {
  line-height: 0.5em;
  color: #241f1c;
}
.subtext-three {
  color: #33002f;
}
.svg-icon {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 8px;
  height: 100%;
}
.icons-light {
  fill: #ffffff;
}
svg:hover,
svg:focus {
  fill: #1572b6;
}

.img-one,
.img-two,
.img-three {
  /* margin-right: 8px; */
  width: 100%;
  height: auto;
  margin: auto;
}

