.container {
  display: flex;
  flex: 1 1 100%;
}
.home-hero-banner {
  flex: 1 1 100%;
  position: relative;
  padding-top: 48px;
  padding-bottom: 48px;
  /* padding: 160 48px 160px 48px; */
  background: #f2ffff;
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(44, 67, 97, 0.5)),
      to(rgba(44, 67, 97, 0.5))
    ),
    url(/static/media/welcome-banner.c0e21d44.jpg);
  background-image: linear-gradient(
      rgba(44, 67, 97, 0.5),
      rgba(44, 67, 97, 0.5)
    ),
    url(/static/media/welcome-banner.c0e21d44.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 50%;
}

.hero-home {
  width: 100%;
  overflow: hidden;
  height: auto;
}
.intro {
  font-size: 2rem;
  padding-top: 10px;
  color: whitesmoke;
  font-family: "Muli", sans-serif;
}
.intro-text {
  color: whitesmoke;
  font-family: "Muli", sans-serif;
  padding: 20px;
  margin: 1.2em 0 1.2em 0;
}

.text-quote {
  color: #070f22;
  padding: 10px;
  line-height: 150%;
  font-size: 1rem;
}
.text-author {
  color: #070f22;
  padding: 10px;
  font-size: 0.8rem;
  font-weight: bold;
}

/* Pattern styles */
.container {
  display: flex;
  flex-flow: wrap;
  text-align: center;
  padding: 0 !important;
}

.quote-container {
  background-color: #f2ffff;
  flex: 1 1 100%;
  padding: 1rem;
}

.image-cropper {
  display: inline-block;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.profile-photo {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 50%;
  overflow: hidden;
}
.contact-me-btn {
  font-family: "Muli", sans-serif;
  font-size: 16px;
  display: inline-block;
  background: #c6e8e9;
  color: #070f22;
  padding: 0.5em 1.5em 0.5em 1.5em;
  border-radius: 50px;
  border: none;
}
.contact-me-btn:hover {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #070f22;
  padding: 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.header-container {
  background-color: #070f22;
  color: whitesmoke;
  flex: 1 1 100%;
  font-family: "Muli", sans-serif;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

@media only screen and (min-width: 500px) {
  .quote-container {
    background-color: #c6e8e9;
    flex: 1 1;
    padding: 1rem;
  }

  .home-hero-banner {
    background-size: cover;
    padding-bottom: 40px;
  }
}

@media only screen and (min-width: 750px) {
  .intro {
    font-size: 4rem;
    padding-top: 50px;
  }
  .quote-container {
    flex: 1 1 100%;
  }
  h1 {
    font-size: 24px;
    font-weight: 900;
    font-style: italic;
  }
  h2 {
    font-size: 48px;
    font-weight: 500;
    line-height: 1.25;
  }
  h2 {
    font-size: 32px;
    font-weight: 500;
    line-height: 1.25;
  }
  .home-hero-banner {
    background-size: cover;
    padding-bottom: 120px;
  }
  .section-static {
    flex: 1 1 100%;
  }
  .contact-me-btn {
    font-weight: bold;
    font-size: 22px;
  }
}


